"use client";

import React, { useEffect } from "react";
import { useRouter } from "next/navigation";
import { Card } from "@/components/ui/card";
import { Button } from "@/components/ui/button";

export default function VerifyPage() {
  const [isVerifying, setIsVerifying] = React.useState(false);
  const [isVerified, setIsVerified] = React.useState(false);
  const [progress, setProgress] = React.useState(0);
  const timerRef = React.useRef<NodeJS.Timeout | null>(null);
  const progressRef = React.useRef<NodeJS.Timeout | null>(null);
  const router = useRouter();

  useEffect(() => {
    const verified = localStorage.getItem("userVerified");
    if (verified === "true") {
      router.push("/");
    }
  }, [router]);

  const handleMouseDown = () => {
    if (!isVerified) {
      setIsVerifying(true);
      setProgress(0);
      
      progressRef.current = setInterval(() => {
        setProgress(prev => Math.min(prev + 2, 100));
      }, 40);

      timerRef.current = setTimeout(() => {
        setIsVerified(true);
        setIsVerifying(false);
        localStorage.setItem("userVerified", "true");
        if (progressRef.current) clearInterval(progressRef.current);
        setTimeout(() => {
          router.push("/");
        }, 1500);
      }, 2000);
    }
  };

  const handleMouseUp = () => {
    if (!isVerified) {
      if (timerRef.current) clearTimeout(timerRef.current);
      if (progressRef.current) clearInterval(progressRef.current);
      setIsVerifying(false);
      setProgress(0);
    }
  };

  return (
    <div className="min-h-screen flex items-center justify-center p-4 bg-gray-50">
      <Card className="max-w-md w-full p-8 shadow-lg">
        <div className="space-y-8">
          <div className="text-center">
            <h1 className="text-3xl font-bold mb-2">
              Bienvenido a La Guelaguetza
            </h1>
            <p className="text-gray-600">
              Para acceder a nuestros productos, primero necesitamos verificar que eres una persona real
            </p>
          </div>

          <div className="space-y-6">
            <div className="text-center">
              <h2 className="text-xl font-semibold mb-2">Verifica tu identidad</h2>
              <p className="text-gray-600 text-sm">
                Mantén presionado el botón durante 2 segundos
              </p>
            </div>

            <div className="relative">
              <Button
                variant={isVerified ? "default" : "outline"}
                className={`
                  w-full h-14 text-lg font-medium transition-all
                  ${isVerified 
                    ? 'bg-green-500 hover:bg-green-600 text-white'
                    : isVerifying
                      ? 'bg-blue-500 hover:bg-blue-500 text-white'
                      : 'hover:border-blue-500'
                  }
                `}
                onMouseDown={handleMouseDown}
                onMouseUp={handleMouseUp}
                onTouchStart={handleMouseDown}
                onTouchEnd={handleMouseUp}
                disabled={isVerified}
              >
                {isVerified 
                  ? "¡Verificación Exitosa!" 
                  : isVerifying 
                    ? "Mantenga Presionado..." 
                    : "Presionar y Mantener"}
              </Button>

              {isVerifying && (
                <div className="absolute bottom-0 left-0 w-full h-1 bg-gray-200 rounded-full overflow-hidden">
                  <div 
                    className="h-full bg-blue-500 transition-all duration-100"
                    style={{ width: `${progress}%` }}
                  />
                </div>
              )}
            </div>

            {isVerified && (
              <div className="p-4 bg-green-50 border border-green-200 rounded-lg">
                <p className="text-green-700 text-center">
                  ¡Verificación exitosa! Serás redirigido automáticamente...
                </p>
              </div>
            )}
          </div>
        </div>
      </Card>
    </div>
  );
}
